 @import "https://js.arcgis.com/4.25/@arcgis/core/assets/esri/themes/dark/main.css";

 *:focus {
  outline: 0;
}

.esri-view .esri-view-surface--inset-outline:focus::after{
  outline: 0;
}

[contenteditable] {
  outline: 0px solid transparent;
}

* {
  box-sizing: border-box;
}


 :root {
  /* --mainBGColor: #282C34; */
  --mainFontColor: rgb(127, 127, 127);
  --mainHoverColor: #00bdbd;

  --mainBGColor: #000;
  --secondBGColor: #f9f9f9;
  
  --fontSize: 25px;
  
  /* --font-family: ''; */
}

body{
  background: var(--mainBGColor);
  color: var(--mainFontColor);

  font-family: 'Source Sans Pro';
  
  /* font-family: var(--font-family); */
}

.bg-second {
  background: var(--secondBGColor);
}

a {
  text-decoration: none;
  color: var(--mainFontColor);
}

a:hover {
  color: var(--mainHoverColor);
  font-weight: 400
}

.active-nav-link {
  color: var(--mainHoverColor);
}

li {
  padding-top: 6px;
  padding-bottom: 6px;
}


/* .scroller {
  width: 300px;
  height: 100px;
  overflow-y: scroll;
  scrollbar-color: rebeccapurple green;
} */

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: var(--secondBGColor) /*  #282c34; */
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--scrollbar) /* #6b6d76; */
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #00bdbd;
}


.fa {
  font-size: 48px;
  color: var(--mainFontColor);
}
.fa-menu {
  font-size: 15px;
  color: var(--mainFontColor);
  padding-right: 3px;
}
.fa-menu:hover {
  color: var(--mainHoverColor);
}

.icon {
  padding-right: 25px;
}

.copyright{
  margin-bottom: 100px;
}


.text-center h1 {
  padding-left: 50px;
  padding-right: 50px;
} 


/* ---------------------------- */
h1 {
  font-size: 50px;
  text-transform: uppercase;
}
h4 {
  padding-top: 40px;
  font-size: 28px;
}
/** reset */
figure, figcaption {
  padding: 0;
  margin: 0;
}

.markdown-padding{
  padding-top: 40px;
  padding-bottom: 70px;

}
.markdown-text-area {
  width: max(55%, 500px);
}
.markdown-bg-light{
  background-color: #fff;
}
.markdown-bg-dark{
  background: var(--mainBGColor);
}

.class-index{
  padding-bottom: 0px;
  margin-bottom: 0px;
  font-size: 50px;
}


/* ---------------------------- */

.title {
  margin-top: 50px;
  margin-bottom: 50px;
  font-size: max(62px, calc(13vw - 120px));
  text-transform: uppercase;
  font-stretch: condensed;
  color: var(--mainFontColorHighlight);

}
.title-sub {
  margin-top: 40px;
  font-size: 32px;
  font-weight: normal;
  text-transform: uppercase;
  font-stretch: condensed;
  color: var(--mainFontColorHighlight);
}
@media (max-width: 768px) {
  .title {
    /* font-size: 65px; */
    font-size: max(48px, calc(3vw));
    padding-top: 50px;
    padding-top: 50px;
  }

}



/* ---------------------------- */


.content-title {
  font-size: max(62px, calc(5vw - 120px)) !important;
  padding-top: 50px;
  line-height: normal;
}
.content-sub-title {
  padding-top: 15px;
  font-size: max(32px, calc(5vw - 120px));
  padding-bottom: 10px;
  line-height: normal;
}
.content-text {
  padding-bottom: 20px;
  color: aquamarine !important;
}

@media (max-width: 768px) {
  .data-design-bg {
    /* font-size: 65px; */
    font-size: max(50px, calc(5vw - 120px));
    /* padding-top: 50px; */
    /* background-color: #00bdbd !important; */
  }
  .content-title{
    font-size: 36px !important;
  }
  .content-text{
    font-size: large;
    color: aquamarine !important;
    padding-right: 10px;
  }
  .index-link {
    font-size: large;
    color: aquamarine !important;
    padding-right: 10px;
  }
  .a {
    font-size: 12px !important;
  }
  .bio-center{
    margin-left: 20px ;
    margin-right: 20px ;
    min-width: 90% !important;
  }
  .projectBinText {
    font-size: small !important;
  }
}
