.layout-event {
    display: flex;
    flex-direction: column;
    height: 100vh;
    font-size: large;
    padding: 50px;

}
@media (max-width: 768px) {
    .layout-event {
      /* font-size: 65px; */
      /* margin: 10px;
      font-size: max(50px, calc(5vw - 120px)); */
      padding-top: 5px;
      padding: 10px;
    }
  
}
.main {
    flex: 1 1 auto;
}

.event-panel {
    background: red;
    /* width: 100%; */
    height: 100%;
    margin: 100px;
    color: aliceblue;

}
@media (max-width: 768px) {
    .event-panel {
      /* font-size: 65px; */
      margin: 10px;
      font-size: max(50px, calc(5vw - 120px));
      padding-top: 50px;
    }
  
}


.bio-center {
    max-width: 60%;
    margin: 0 auto;
    /* padding-bottom: 80px; */
}

.text-center-panel {
    font-size: max(100px, calc(10vw));
    font-weight: 900;
    padding-top: 80px;
    padding-left: 80px;
    padding-right: 80px;
    text-align: center;
}
@media (max-width: 768px) {
    .text-center-panel{
        font-size: max(80px, calc(10vw));
        padding-top: 20px;
        padding-left: 10px;
        padding-right: 10px;
        padding-bottom: 20px;
    }
  
}
.text-center-paragraph-panel {
    font-size: x-large;
    padding-top: 20px;
    text-align: center;
    padding-left: 80px;
    padding-right: 80px;
}
@media (max-width: 768px) {
    .text-center-paragraph-panel{
        padding-top: 20px;
        padding-left: 10px;
        padding-right: 10px;
        padding-bottom: 20px;
    }
  
}
.qr {
    padding-top: 40px;
    padding-bottom: 40px;
    padding-left: 40%;
    padding-right: 40%;
    width: 100%;

}