.njcTitle {
    color: var(--mainFontColor);
    font-size: small;
    overflow: hidden;
}
.njcTitle:hover {
    color: var(--mainHoverColor);
    font-weight: 400
  }

.njcText {
    margin-top: 10px;
    color: #777;
    font-size:x-small;
    text-overflow: ellipsis;
    overflow: hidden;
}

.timeLineText {
    display: block;
}

.timeLineTextUnderline{
    text-decoration: underline;
}
.timeLineTextUnderline:hover{
    color: var(--mainHoverColor);
}