/* ReadMore.css */
.readMoreContainer {
  position: relative;
  max-width: 1000px; /* 필요에 따라 조정 */
  overflow: hidden;
}

.readMoreContent {
  overflow: hidden;
  transition: max-height 0.9s ease-in-out, padding 0.9s ease-in-out;
  box-sizing: border-box;
  height: auto;
}

.readMoreContent.collapsed {
  max-height: 600px;
}

.readMoreContent.expanded {
  max-height: 2000px; /* 충분히 큰 값으로 설정 */
}

.readMoreButton {
  background-color: rgba(0,0,0,0);
  color: var(--mainFontColorHighlight);
  border: none;
  padding: 5px 10px;
  margin-top: 10px;
  cursor: pointer;
  border-radius: 3px;
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
  bottom: 0;
  left: 0;
}

.readMoreButton:hover {
  background-color: var(--mainHoverColor);
  color: var(--mainFontColorHighlight);
}
