.img {
    filter: grayscale(100%);
    width:100%;
    padding-top: 20px;
    padding-bottom: 20px;
}
.img:hover {
    filter: grayscale(0%);
}

.index-container-main-title{
    text-align: center;
}

.index-bg{
    background-color: white;
    padding-bottom: 60px;
}

.text-center {
    font-size: max(62px, calc(9vw - 120px));
    padding-top: 80px;
    text-align: center;
}
.text-center-paragraph {
    font-size: x-large;
    padding-top: 20px;
    text-align: center;
}

.fa-icon {
    font-size: 10px;
    margin-right: 20px;
}
.fa-icon-social {
    font-size: 26px;
    padding-right: 10px;
}