.text {
  font-size: small;
  margin-top: 0px;
  margin-bottom: 0px;

}

.toc ul {
  list-style-type: none;
  padding-left: 10px;
  margin-top: 0px;
  
}

.toc a.active {
  color: var(--mainHoverColor);
  font-weight: bold;
}

.toc a {
  text-decoration: none;
  /* color: #333; */
  color: var(--mainFontColor);
  transition: color 0.3s ease;
}
.toc:hover {
  color: var(--mainHoverColor);
  transition: color 0.3s ease;
}


.toc li.active > a {
  color: var(--mainHoverColor);
  font-weight: bold;
}
