.image-cropper {
    width: 300px;
    height: 300px;
    position: relative;
    overflow: hidden;
    border-radius: 50%;

    filter: grayscale(1);
}
.bio-center a{
    /* color:brown !important; */
    text-decoration: underline;
    /* font-weight: 600 !important; */
}
.img {
    filter: grayscale(100%);
    width:100%;
    padding-top: 20px;
    padding-bottom: 20px;
}
.img:hover {
    filter: grayscale(0%);
}
.bio-container{
    /* background-color: white; */
    /* max-width: 80%; */
    /* margin: 0 auto; */
}
.bio-container-main-title{
    text-align: center;
}

.bio-bg{
    background-color: var(--secondBGColor);
    padding-bottom: 60px;
}


.bio-bg-dark{
    padding-bottom: 20px;
}

.content-title {
    color: var(--mainFontColorHighlight);
    margin-top: 10px;
    margin-bottom: 10px;
}
.content-title-sub {
    color: var(--mainFontColorHighlight) !important;
    font-weight: 600;
}

.bio-center {
    max-width: 60%;
    margin: 0 auto;
    /* padding-bottom: 80px; */
}
.bio-center p {
    font-size: large;
}
.text-center {
    font-size: max(62px, calc(9vw - 120px));
    padding-top: 80px;
    text-align: center;
}
.text-center-paragraph {
    font-size: x-large;
    padding-top: 20px;
    text-align: center;
}
.text-style {
    font-weight: 200;
    line-height: 1.8;
    /* line-height: ; */
    /* color: blue; */
    /* font-family: 'Dotum'; */
}
.korean-content {
    font-size: x-small;
}

@media (max-width: 768px) {
    .bio-center {
        max-width: 90%;
        margin-right: 0px;
        margin: 2% auto;
        padding: 1%;
    }
