.open-talk {
    display: flex;
    flex-direction: column;
    /* height: 100vh; */
    font-size: large;

    background: #f00;
    color: #fff;
}
.open-talk-colored{
    padding: 50px;
    background: #f00;
}
.open-talk-white{
    padding: 50px;
    background: whitesmoke;
    color: #f00;
    font-size: 5vw;
}

.title-open {
    margin-top: 50px;
    margin-bottom: 50px;
    font-size: calc(20vw);
    font-weight: 900;
    /* font-size: calc(300%); */
}
.title-sub-open {
    font-size: calc(6vw);
    font-weight: 900;
    padding-bottom: 20px;
    /* font-size: calc(300%); */
}
.content-open {
    font-size: 5vw;
    /* text-size-adjust: ; */
}

.link-open-talk{
    font-weight: bolder;
    color: #f00;
    text-decoration: underline;
    text-decoration-thickness: 2px;
    /* text-decoration-color: black; */
    text-underline-offset: 4px;
}
.link-open-talk:hover{
    font-weight: bolder;
    color: #f00;
    text-decoration: underline;
    text-underline-offset: 4px;
}
.text {
    font-size: 24px;
}

.text-step {
    font-size: 3vw;
    padding-bottom: 30px;
}
@media (max-width: 768px) {
    .text-step {
        font-size: 24px;
        padding-bottom: 30px;
    }
  
  }