.text-center {
    font-size: max(62px, calc(9vw - 120px));
    padding-top: 80px;
    text-align: center;
    /* margin: 7% */
}
.project-bg-dark {
    background: var(--mainBGColor);
    padding-bottom: 60px;
    margin-bottom: 60px !important;
}
.research-Project-bg {
    /* padding: 5px; */
    /* display: flex; */
    /* flex-wrap: wrap; */
    /* display: flex;
    flex-direction: column; */

    padding-left: 5%;
    padding-right: 5%;

    margin-bottom: 50px;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-flow: wrap;
}

.project-bg {
    padding: 5%;
}

.projectBin {
    width: 150px;
    height: 150px;

    font-size: large;
    background-color: var(--mainBGColor);  /* lightgray; */

    border-radius: 5px;
    margin: 20px;
    margin-bottom: 100px;
    /* opacity: 0.65; */

    /* filter: saturate(0.0); */

    transition: box-shadow .29s ease-out;
    -moz-transition: box-shadow .29s ease-out;
    -webkit-transition: box-shadow .29s ease-out;
    -o-transition: box-shadow .29s ease-out;
    /* background-size: 200px 200px; */
}

/* https://htmlcssfreebies.com/css-box-shadow-examples/ */
.projectBin:hover {
    /* filter: saturate(1.1); */
    /* box-shadow: 0 0 10px 4px #888 !important; */

    /* box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px; */
    /* box-shadow: 0 60px 80px rgba(0,0,0,0.60), 0 45px 26px rgba(0,0,0,0.14); */
    box-shadow: 0 25px 50px -12px rgba(0,0,0,0.35);
    /* box-shadow: 0 1px 1px rgba(0,0,0,0.12), 0 2px 2px rgba(0,0,0,0.12), 0 4px 4px rgba(0,0,0,0.12), 0 8px 8px rgba(0,0,0,0.12), 0 16px 16px rgba(0,0,0,0.12); */
}

/* Popup style */

.close-icon {
    content: 'x';
    cursor: pointer;
    position: fixed;
    right: calc(15% - 18px);
    top: calc(100vh - 85vh - 42px);
    background: #f6f7f9;
    width: 45px;
    height: 45px;
    border-radius: 50%;
    line-height: 20px;
    text-align: center;
    /* border: 3px solid #999; */
    font-size: 40px;
    padding-top: 8px;
}

.close-icon:hover {
    background: #00bdbd;
    color: #fff;
}

iframe {
    border: 0;
}

