/* Popup style */

.close-icon {
    content: 'x';
    cursor: pointer;
    position: fixed;
    right: calc(15% - 18px);
    top: calc(100vh - 85vh - 42px);
    background: #f6f7f9;
    width: 45px;
    height: 45px;
    border-radius: 50%;
    line-height: 20px;
    text-align: center;
    /* border: 3px solid #999; */
    font-size: 40px;
    padding-top: 8px;
}

.close-icon:hover {
    background: #00bdbd;
    color: #fff;
}

iframe {
    border: 0;
}