.header-menu {
    display: flex;
    /* height: 200px; */
}
.header-menu ul {
    display: flex;
    justify-content:center;
    align-items: center;

    /* flex-direction: column; */
    
    list-style: none;
    width: 100%;
    gap: 25px;
    padding-left: 0px;
}

.header-menu-direction li {
 text-align: center;    
}

.njs-icon {
    display: flex;
    /* justify-content: flex-start; */
    justify-content: center;
    align-items: center;

    font-weight: 500;
    /* color:#00bdbd; */
    font-size: 30px;
    margin-right: 0px;
}
.sticky {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: var(--mainBGColor);
    z-index: 5;
}

@media (max-width: 768px) {
    .header-menu-direction{
        flex-direction: column;
    }
    .header-menu-direction.lab{
        flex-direction: row;
    }
    .header-menu-direction.lab .top-menu-text{
        font-size:  0px;
    }
     
    .header-menu-direction li {
        padding: 0px;
    }
    .separator-vertical{
        font-size: 8px;
    }
  
}

/* UI menu size  */
@media (max-width: 1300px) {
    .header-menu ul {
        display: flex;
        justify-content:center;
        align-items: center;
    
        /* flex-direction: column; */
        
        list-style: none;
        width: 100%;
        gap: 10px;
        padding-left: 0px;
        font-size: medium;
    }
}

.loginBtn {
    background-color: rgba(0,0,0,0);
    border: 0px;
    color: var(--mainMenuText);
}

.loginBtn:hover {
    color: #999;
}